<template>
    <main id="art-cycle" class="p-m-2 p-input-filled">
        <div class="p-grid">
            <div class="p-col-12 p-md-6">
                <art-cycle-patient-search />
            </div>
            <div class="p-col-12 p-md-6">
                <art-cycle-partner-search />
            </div>
        </div>

        <div class="p-grid">
            <div class="p-col-12">
                <art-cycle-form />
            </div>
        </div>

        <div style="height: calc(100vh - 510px)">
            <art-cycle-summary />
        </div>

        <div class="p-grid p-mt-1">
            <div class="p-col-12 p-md-6">
                <art-cycle-lab-test type="female" />
            </div>
            <div class="p-col-12 p-md-6">
                <art-cycle-lab-test type="male" />
            </div>
        </div>

        <p-button type="button" label="Save" class="p-mr-2"></p-button>
    </main>
</template>

<script>
import { computed, provide, ref } from "vue";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";

import ArtCyclePatientSearch from "@/components/art-cycle/patient-search.vue";
import ArtCyclePartnerSearch from "@/components/art-cycle/partner-search.vue";
import ArtCycleSummary from "@/components/art-cycle/summary.vue";
import ArtCycleForm from "@/components/art-cycle/form.vue";
import ArtCycleLabTest from "@/components/art-cycle/lab-test.vue";

export default {
    components: {
        ArtCyclePatientSearch,
        ArtCyclePartnerSearch,
        ArtCycleSummary,
        ArtCycleForm,
        ArtCycleLabTest,
    },
    setup() {
        const store = useStore();

        const cyclesSummary = computed(
            () => store.state.artCycle.cyclesSummary
        );
        const patient = ref();
        const partner = ref();
        const cycleTypes = ref();

        provide("cycles", cyclesSummary);
        provide("patient", patient);
        provide("partner", partner);
        provide("cycleTypes", cycleTypes);

        const getCycles = ({ demographicNo }) => {
            store.dispatch("artCycle/getCyclesDetails", demographicNo);
        };

        emitter.on("patient-search:patient-selected", getCycles);

        return {
            cyclesSummary,
        };
    },
};
</script>

<style>
#art-cycle .p-card .p-card-content {
    padding: 0;
}
</style>